import logo from '../../assets/images/CZ_LOGO3.png';
import styles from './Header.module.scss';
import { useTranslation } from 'react-i18next';
import { useNavigate, useLocation } from 'react-router-dom';
import sidebarStyles from '../sidebar/Sidebar.module.scss';
import { useState, useEffect } from 'react';

interface HeaderProps {
    onMenuClick?: () => void;
    isSidebarOpen?: boolean;
    onScrollToSection?: (sectionId: string) => void;
}

const Header = ({ onMenuClick, isSidebarOpen, onScrollToSection }: HeaderProps) => {
    const { i18n, t } = useTranslation();
    const navigate = useNavigate();
    const location = useLocation();

    const changeLanguage = (e: React.ChangeEvent<HTMLSelectElement>) => {
        const lng = e.target.value;
        i18n.changeLanguage(lng);
        localStorage.setItem('language', lng);
    };

    const handleNewsClick = (e: React.MouseEvent) => {
        e.preventDefault();
        // 
        window.scrollTo(0, 0);
        document.documentElement.scrollTop = 0;
        document.body.scrollTop = 0;
        // 
        navigate('/news');
        // 
        setTimeout(() => {
            window.scrollTo(0, 0);
            document.documentElement.scrollTop = 0;
            document.body.scrollTop = 0;
        }, 50);
        setTimeout(() => {
            window.scrollTo(0, 0);
            document.documentElement.scrollTop = 0;
            document.body.scrollTop = 0;
        }, 150);
        setTimeout(() => {
            window.scrollTo(0, 0);
            document.documentElement.scrollTop = 0;
            document.body.scrollTop = 0;
        }, 300);
    };

    const scrollToSection = (sectionId: string) => {
        if (onScrollToSection) {
            onScrollToSection(sectionId);
        } else {
            // Fallback prop
            const element = document.getElementById(sectionId);
            if (element) {
                element.scrollIntoView({ behavior: 'smooth', block: 'start' });
            }
        }
    };

    const handleLogoClick = (e: React.MouseEvent) => {
        e.preventDefault();
        if (location.pathname !== '/') {
            navigate('/');
        } else {
            scrollToSection('home');
        }
    };

    return (
        <header className={styles.header}>
            <div className={styles.waterBackground}></div>
            {/*  */}
            <button 
                className={`${sidebarStyles['sidebar-toggle']} ${isSidebarOpen ? sidebarStyles.open : ''}`}
                onClick={onMenuClick}
                type="button"
            >
                <span></span>
            </button>
            <a 
                href="#home" 
                className={styles.logo}
                onClick={handleLogoClick}
            >
                <img src={logo} className={styles['logo-img']} alt="Logo" />
                <span className={styles['company-name']}></span>
                <span className={styles.slogan}>{t('header.slogan')}</span>
            </a>
            <nav className={styles.navbar}>
                <a 
                    href="#about"
                    onClick={(e) => {
                        e.preventDefault();
                        scrollToSection('about');
                    }}
                >
                    {t('nav.about')}
                </a>
                <a 
                    href="#services"
                    onClick={(e) => {
                        e.preventDefault();
                        scrollToSection('services');
                    }}
                >
                    {t('nav.services')}
                </a>
                <a 
                    href="#/news"
                    onClick={handleNewsClick}
                >
                    {t('nav.news')}
                </a>
                <a 
                    href="#contact"
                    onClick={(e) => {
                        e.preventDefault();
                        scrollToSection('contact');
                    }}
                >
                    {t('nav.contact')}
                </a>
            </nav>
            <div className={styles['language-switcher']}>
                <select 
                    value={i18n.language}
                    onChange={changeLanguage}
                    className={styles['language-select']}
                >
                    <option value="zh-TW"></option>
                    <option value="en-US">English</option>
                    <option value="ja-JP"></option>
                </select>
            </div>
        </header>
    )
}
export default Header;