import { useState, useEffect } from 'react';
import { ChevronLeft, ChevronRight } from 'lucide-react';
import styles from './Banner.module.css';

interface BannerProps {
  banners: Array<{
    img: string;
    alt: string;
    text: string;
  }>;
  backgroundImage?: string;
}

export default function Banner({ banners, backgroundImage }: BannerProps) {
  const [bannerIndex, setBannerIndex] = useState(0);
  const [autoPlay, setAutoPlay] = useState(true);
  const [touchStart, setTouchStart] = useState(0);
  const [touchEnd, setTouchEnd] = useState(0);

  useEffect(() => {
    if (!autoPlay) return; // 

    const timer = setInterval(() => {
      setBannerIndex((prev) => (prev + 1) % banners.length);
    }, 5500);
    return () => clearInterval(timer);
  }, [banners.length, autoPlay]);

  const handleManualChange = (newIndex: number) => {
    setBannerIndex(newIndex);
    setAutoPlay(false); // 
  };

  // 
  const handleTouchStart = (e: React.TouchEvent) => {
    setTouchStart(e.targetTouches[0].clientX);
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    setTouchEnd(e.targetTouches[0].clientX);
  };

  const handleTouchEnd = () => {
    if (!touchStart || !touchEnd) return;

    const distance = touchStart - touchEnd;
    const minSwipeDistance = 50; // 

    if (Math.abs(distance) < minSwipeDistance) return;

    if (distance > 0) {
      // 
      handleManualChange((bannerIndex + 1) % banners.length);
    } else {
      // 
      handleManualChange((bannerIndex - 1 + banners.length) % banners.length);
    }

    // 
    setTouchStart(0);
    setTouchEnd(0);
  };

  return (
    <section className={styles.bannerSection}>
      {/*  */}
      {backgroundImage && (
        <div
          className={styles.bannerBackground}
          style={{ backgroundImage: `url(${backgroundImage})` }}
        />
      )}
      <button
        className={styles.bannerBtn}
        onClick={() => {
          handleManualChange((bannerIndex - 1 + banners.length) % banners.length);
        }}
      >
        <svg width="0" height="0" style={{ position: 'absolute' }}>
          <defs>
            <linearGradient id="arrowGradient" x1="0%" y1="0%" x2="0%" y2="100%">
              <stop offset="0%" stopColor="#ffffff" stopOpacity="1" />
              <stop offset="45%" stopColor="#ffffff" stopOpacity="1" />
              <stop offset="55%" stopColor="#999999" stopOpacity="1" />
              <stop offset="100%" stopColor="#555555" stopOpacity="1" />
            </linearGradient>
          </defs>
        </svg>
        <div style={{ position: 'relative', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
          {/*  -  */}
          <ChevronLeft
            size={40}
            strokeWidth={7}
            style={{
              stroke: 'url(#arrowGradient)',
              position: 'absolute',
              opacity: 0.7
            }}
            absoluteStrokeWidth
          />
          {/*  -  */}
          <ChevronLeft
            size={42}
            strokeWidth={4}
            style={{ stroke: 'url(#arrowGradient)' }}
            absoluteStrokeWidth
          />
        </div>
      </button>

      <div
        className={`${styles.bannerContent} ${banners[bannerIndex].img ? styles.hasImage : styles.noImage}`}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      >
        {banners[bannerIndex].img && (
          <img
            src={banners[bannerIndex].img}
            alt={banners[bannerIndex].alt}
            className={styles.bannerImg}
            loading="eager"
            fetchPriority="high"
            decoding="sync"
          />
        )}
        <div className={styles.bannerText}>
          {banners[bannerIndex].text}
        </div>
      </div>

      <button
        className={styles.bannerBtn}
        onClick={() => {
          handleManualChange((bannerIndex + 1) % banners.length);
        }}
      >
        <div style={{ position: 'relative', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
          {/*  -  */}
          <ChevronRight
            size={40}
            strokeWidth={7}
            style={{
              stroke: 'url(#arrowGradient)',
              position: 'absolute',
              opacity: 0.7
            }}
            absoluteStrokeWidth
          />
          {/*  -  */}
          <ChevronRight
            size={42}
            strokeWidth={4}
            style={{ stroke: 'url(#arrowGradient)' }}
            absoluteStrokeWidth
          />
        </div>
      </button>

      {/* Banner  */}
      <div className={styles.bannerIndicators}>
        {banners.map((_, index) => (
          <button
            key={index}
            className={`${styles.indicator} ${index === bannerIndex ? styles.indicatorActive : ''}`}
            onClick={() => handleManualChange(index)}
            aria-label={` banner ${index + 1}`}
          />
        ))}
      </div>
    </section>
  );
}