import { useState, useEffect } from 'react';
import styles from '../../pages/sections/home/HomeSection.module.css';
import { motion, AnimatePresence } from 'framer-motion';
import { fadeNormal, fadeLast } from '../../utils/animations/motionPresets';
import texts from './introTexts';

type Props = {
  onFinish?: () => void;
};

export default function Intro({ onFinish }: Props) {
  const [index, setIndex] = useState(0);
  const [leaving, setLeaving] = useState(false);
  const [show, setShow] = useState(true); //  true

  const isLast = index === texts.length - 1;

  //  localStorage
  useEffect(() => {
    const seen = localStorage.getItem('introShown');
    if (seen) {
      setShow(false); //   
      onFinish?.();
    }
    // show  true
  }, [onFinish]);

  useEffect(() => {
    if (!show || leaving) return; //   
    const timer = setTimeout(() => {
      if (!isLast) {
        setIndex(prev => prev + 1);
      } else {
        setLeaving(true); //   
        localStorage.setItem('introShown', 'true'); // 
      }
    }, isLast ? 4000 : 3000);

    return () => clearTimeout(timer);
  }, [index, isLast, leaving, show]);

  return (
    <div className={styles.welcomeWrapper} aria-live="polite">
      <AnimatePresence
        mode="wait"
        onExitComplete={() => {
          if (leaving) {
            onFinish?.();
          }
        }}
      >
        {show && !leaving && (
          <motion.div
            key={index}
            variants={isLast ? fadeLast : fadeNormal}
            initial="initial"
            animate="animate"
            exit="exit"
            className={styles.welcomeText}
            style={{
              fontSize: '28px',
              lineHeight: 1.2,
              textAlign: 'center',
            }}
          >
            {texts[index].split('\n').map((line, i, arr) => (
              <span key={i}>
                {line}
                {i < arr.length - 1 && <br />}
              </span>
            ))}
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
